div.flagrate-search-box {
    width: 240px;
    height: 32px;
    display: inline-block;

    > input.search-input {
        width: 100%;
        height: 100%;
        padding-right: 30px;
    }

    > button.search-button {
        position: absolute;
        top: 3px;
        right: 3px;
        bottom: 3px;
        height: auto;
        width: 24px;
        box-shadow: none;
        border-color: transparent;
        transition-property: background-color;
        background-color: transparent;
        background-size: 12px 12px;
        background-position: center center;
        background-image: url('data:image/svg+xml;base64,');

        &:disabled {
            opacity: 0.5;
        }

        &:focus {
            &:extend(button.flagrate-button:focus);
        }
    }

    > button.search-button:hover,
    > button.search-button:focus,
    > input.search-input:focus + button.search-button {
        &:not(:disabled) {
            background-color: @primary;
            background-image: url('data:image/svg+xml;base64,');
        }
    }

    > button.search-button:active,
    > input.search-input:focus + button.search-button:hover {
        background-color: @primary-hover;
    }

    > div.search-suggest {
        position: absolute;
        z-index: 1;
        top: 100%;
        left: 0;
        width: 100%;
        max-height: 200px;
        margin: 4px 0 0 0;
        overflow-x: hidden;
        overflow-y: auto;
        border: 1px solid @outline;
        box-shadow: 2px 2px 0 fade(black, 10%);
        box-sizing: border-box;

        &.flagrate-search-suggest-upper {
            top: auto;
            bottom: 100%;
            margin: 0 0 4px 0;
        }

        > div.flagrate-menu {
            width: 100%;
            border: none;

            > button.flagrate-search-suggest-selected {
                color: @primary-inverse;
                background-color: @primary;
            }
        }
    }
}